import * as React from 'react';
import './End.css';
import {sleep} from "../game/sleep";
import {game, GameState} from "../game/Game";

interface IEndState {
    opacity: number;
    message: string;
}

export class End extends React.Component<{}, IEndState> {
    state: IEndState = {
        opacity: 0,
        message: ""
    }

    async componentDidMount() {
        await sleep(1000);
        await this.show("Aprs une longue & aventureuse nuit. Notre rveuse se rveilla au chaud chez elle. Le sourire aux lvres.");
        await this.show("Kstore et Acidpop continurent leurs explorations dans les rves,  la recherche de nouveaux compagnons.");
        await this.show("Shrek dcida d'offrir des cadeaux aux morts-vivants restant, esprant les rendre gentils.");
        await this.show("Sindragoseur s'est endormi une dernire fois, la rose des rves  ses cts.");
        await this.show("Quant  Renard, il resta au mme endroit. Esprant revoir la rveuse une nouvelle fois.");
        this.setState({opacity: 1, message: "Fin"});
        await sleep(12000);
        game.state = GameState.TITLE;
    }

    async show(message: string) {
        this.setState({opacity: 1, message})
        await sleep(12000);
        this.setState({opacity: 0})
        await sleep(4000);
    }

    render() {
        return (
            <div className="End">
                <div className="text" style={{opacity: this.state.opacity}}>
                    {this.state.message}
                </div>
            </div>
        );
    }
}